.desktop-welcome {
	width: 100%;
	text-align: center;
  transform: scale(0.94);
  animation: scale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}

@keyframes scale {
  100% {
    transform: scale(1);
  }
}

.desktop-name {
	color: #00ffff;
	font-size: 3.375rem;
	font-weight: bold;
	letter-spacing: 0.5rem;
}

.desktop-dev {
	margin-top: 2%;
	color: #e0ae77;
	font-size: 1.125rem;
}

.home-desktop-about-link {
  margin-top: 4%;
}

.desktop-about-link {
	color: #e0ae77;
  background-color: transparent;
	border: none;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.desktop-about-link:hover {
	color: #00ffff;
}

.desktop-about-link:hover::after {
	display: inline-block;
	width: 0.75rem;
	height: 0.75rem;
	content: url(../../images/arrow.svg);
  animation-name: rightArrow;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

@keyframes rightArrow {
  0% {
    margin-left: 0.1rem;
  }
  100% {
    margin-left: 0.5rem;
  }
}

.desktop-welcome span {
  display: inline-block;
  opacity: 0;
  filter: blur(4px);
}

