.mobile-welcome {
  width: 100%;
  text-align: center;
	transform: scale(0.94);
  animation: scale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}

@keyframes scale {
  100% {
    transform: scale(1);
  }
}

.mobile-me {
  width: max-content;
  margin: 0 auto;
}

.mobile-first-name {
	padding-left: 1rem;
  color: #00ffff;
	font-size: 3.375rem;
	font-weight: bold;
	letter-spacing: 1.5rem;
}

.mobile-last-name {
  color: #00ffff;
	font-size: 3.375rem;
}

.mobile-dev {
  margin-top: 2%;
	color: #e0ae77;
	font-size: 1.125rem;
}

.home-mobile-about-link {
  margin-top: 4%;
}

.mobile-about-link {
	color: #e0ae77;
	background-color: transparent;
	border: none;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.mobile-about-link:hover {
		color: #00ffff;
	}
	
	.mobile-about-link:hover::after {
		display: inline-block;
		width: 0.75rem;
		height: 0.75rem;
		content: url(../../images/arrow.svg); 
		animation-name: rightArrow;
		animation-duration: 0.4s;
		animation-timing-function: ease-in-out;
		animation-direction: alternate;
		animation-iteration-count: infinite;
	}

}

.touch-link {
	color: #00ffff;
}

@keyframes rightArrow {
  0% {
    margin-left: 0.1rem;
  }
  100% {
    margin-left: 0.5rem;
  }
}

.mobile-me span {
	display: block;
	margin: 2.5rem auto;
  opacity: 0;
  filter: blur(4px);
}

.home-mobile-about-link span {
  opacity: 0;
  filter: blur(4px);
}

.mobile-me span:nth-child(1) {
  animation: fade-in 0.8s 0.4s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.mobile-me span:nth-child(2) {
  animation: fade-in 0.8s 0.8s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.mobile-me span:nth-child(3) {
  animation: fade-in 0.8s 1.2s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.home-mobile-about-link span:nth-child(1) {
  animation: fade-in 0.8s 2.0s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}

@media screen and (max-width: 700px) {

	.mobile-first-name {
		font-size: 1.5rem;
		letter-spacing: 1rem;
	}

	.mobile-last-name {
		font-size: 1.5rem;
	}

	.mobile-dev {
		font-size: 0.75rem;
	}

	.mobile-about-link {
		font-size: 0.75rem;
	}

	@media(hover: hover) {

		.mobile-about-link:hover::after {
			width: 0.5rem;
			height: 0.5rem;
		}

	}

	.mobile-me span {
		margin: 1.25rem auto;
	}

}
