header {
	display: block;
	margin-bottom: auto;
	padding: 1.5rem;	
	background-color: #ffffff;
	border-bottom-left-radius: 1rem;
	border-bottom-right-radius: 1rem;
	background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
}

.header-nav {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
}

.header-sub-nav {
	display: flex;
	width: 30rem;
	justify-content: space-evenly;
	align-items: center;
}

.header-current-link {
	color: #00ffff;
	background-color: transparent;
	border: none;
	font-size: 2rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.header-link {
	color: #e0ae77;
	background-color: transparent;
	border: none;
	font-size: 1.125rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.header-link:hover {
		color: #00ffff;
	}

}

.touch-link {
	color: #00ffff;
}

@media screen and (max-width: 700px) {

	header {
		padding: 0.75rem;
	}

	.header-sub-nav {
		width: max-content;
	}

	.header-current-link {
		font-size: 1rem;
		text-transform: uppercase;
	}

	.header-link {	
		font-size: 1rem;
	}

}

@media screen and (max-width: 360px) {

	.header-link {	
		font-size: 0.75rem;
	}

}
