.page-modal-btn {
  position: absolute;
  top: 0;
  right: 0;
	color: #000000;
	background-color: #ffffff;
	border: none;
  font-family: 'Arial', sans-serif;
	font-size: 0.9375rem;
	font-weight: bold;
	cursor: pointer;
}

.policies-link {
  padding: 0;
	color: #ffffff;
  background-color: #3a444f;
  border: none;
	font-size: 1rem;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.policies-link:hover {
		color: #00ffff;
	}

}

.touch-link {
	color: #00ffff;
}

@media screen and (max-width: 700px) {

  .policies-link {
    font-size: 0.75rem;
  }

}
