html {
	font-size: 16px;
}

body {
	margin: 0px;
	background-image: url("./images/space.png");
	background-size: 100%;
	font-family: 'Arial', sans-serif;
	text-align: justify;
	line-height: 2.5rem;
}

#parent-div {
	display:flex; 
	max-width: 100%;
	min-height: 100vh;
  flex-direction: column;
}

main {
	display: flex;
	flex-grow: 1;
	align-items: center;
}

@media screen and (max-width: 700px) {

	body {
		line-height: 1.25rem;
	}

}
