.about-container {
	display: block;
  width: 60%;
  margin: 3rem auto;
	color: #ffffff;
	text-align: center;
}

.about-heading {
	margin-bottom: 3rem;
	font-size: 2rem;
}

.about-img {
	width: 25%;
	border-radius: 50%;
}

.about-text {
	font-size: 1.125rem;
	text-align: justify;
  text-indent: 4.5rem;
}

.about-text a {
  color: #ffffff;
}

.stack-list {
	display: flex;
	margin-left: 5rem;
	margin-bottom: 1rem;
  text-indent: 0;
}

.stack-list-item {
	margin: 0.5rem;
	padding: 1rem;
  width: fit-content;
  color: #ffffff;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.about-portfolio-link {
  margin-top: 8%;
}

.portfolio-link {
	color: #e0ae77;
	/* background-color: #3a444f; */
	background-color: transparent;
	border: none;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.portfolio-link:hover {
		color: #00ffff;
	}
	
	.portfolio-link:hover::after {
		display: inline-block;
		width: 0.75rem;
		height: 0.75rem;
		content: url(../../images/arrow.svg);
		animation-name: rightArrow;
		animation-duration: 0.4s;
		animation-timing-function: ease-in-out;
		animation-direction: alternate;
		animation-iteration-count: infinite;
	}

}

.touch-link {
	color: #00ffff;
}

@keyframes rightArrow {
  0% {
    margin-left: 0.1rem;
  }
  100% {
    margin-left: 0.5rem;
  }
}

@media screen and (max-width: 700px) {

	.about-heading {
		margin-bottom: 1.5rem;
		font-size: 1rem;
	}

	.about-img {
		width: 50%;
	}

	.about-text {
		font-size: 0.75rem;
		text-indent: 1.5rem;
	}

	.stack-list {
		margin-left: 0;
		flex-wrap: wrap;
	}

	.stack-list-item {
		margin: 0.25rem;
		padding: 0.5rem;
	}

	.portfolio-link {
		font-size: 0.75rem;
  }

	@media(hover: hover) {

		.portfolio-link:hover::after { 
			width: 0.5rem;
			height: 0.5rem;
		}

	}
	
}
