.portfolio-container {
	display: block;
	width: 100%;
  margin: 3rem auto;
	color: #ffffff;
	text-align: center;
}

.portfolio-sub-container {
  width: 60%;
  margin: 0 auto;
}

.portfolio-heading {
	margin-bottom: 3rem;
	font-size: 2rem;
}

.portfolio-text {
	font-size: 1.125rem;
	text-align: justify;
	text-indent: 4.5rem;
}

.portfolio-search-input {
	width: 20rem;
	height: 2rem;
	margin-bottom: 3rem;
	background-color: #ffffff;
}

.portfolio-search-input::placeholder {
	font-size: 0.9375rem;
	text-align: center;
}

.portfolio-radio {
	margin-bottom: 3rem;
	font-size: 0.9375rem;
	font-weight: bold;
}

.portfolio-content-list {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.portfolio-content-btn {
	color: #ffffff;
	background-color: #3a444f;
	border: none;
	font-size: 1.5rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.portfolio-content-outer-box {
	display: block;
	width: 60%;
	border-top: 0.1rem solid #ffffff;
	border-bottom: 0.1rem solid #ffffff;
}

.portfolio-content-video {
	margin-top: 2.5rem;
	width: 80%;
}

.portfolio-content-inner-box {
	width: 100%;
}

.portfolio-content-info-one {
	font-size: 1.3125rem;
	font-weight: bold;
}

.portfolio-content-info-two {
	font-size: 0.9375rem;
	font-weight: bold;
}

.portfolio-content-link {
	display: block;
	margin: 1.5rem auto;
	color: #ffffff;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.portfolio-in-progress {
	width: 20rem;
}

@media(hover: hover) {

	.portfolio-content-btn:hover {
		color: #00ffff;
	}

	.portfolio-content-link:hover {
		color: #00ffff;
	}

}

.touch-link {
	color: #00ffff;
}

@media screen and (max-width: 700px) {

	.portfolio-container {
		margin: 1.5rem auto;
	}

	.portfolio-heading {
		margin-bottom: 1.5rem;
		font-size: 1rem;
	}

	.portfolio-text {
		font-size: 0.75rem;
		text-indent: 1.5rem;
	}

	.portfolio-search-input {
		width: 10rem;
		height: 1rem;
		margin-bottom: 1.5rem;
	}

	.portfolio-search-input::placeholder {
		font-size: 0.625rem;
	}

	.portfolio-radio {
		margin-bottom: 1.5rem;
		font-size: 0.75rem;
	}

	.portfolio-radio input {
		vertical-align: sub;
	}

	.portfolio-content-btn {
		font-size: 1rem;
	}

	.portfolio-content-video {
		margin-top: 1.25rem;
		width: 100%;
	}

	.portfolio-content-info-one {
		font-size: 1rem;
	}
	
	.portfolio-content-info-two {
		font-size: 0.75rem;
	}

	.portfolio-content-link {
		margin: 0.75rem auto;
		font-size: 0.75rem;
	}

	.portfolio-in-progress {
		width: 10rem;
	}

}
