footer {
	display: flex;	
	margin-top: auto;
	padding: 0.3rem;
	color: #e0ae77;
	background-color: #ffffff;
	border-top-left-radius: 1rem;
	border-top-right-radius: 1rem;
	font-size: 0.9375rem;
	font-weight: bold;
	justify-content: space-evenly;
	align-items: center;
	flex-wrap: wrap;
	background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(1px);
}

.footer-text {
	margin-left: 1rem;
	margin-right: 1rem;
}

.footer-nav {
	display: flex;
	width: 15rem;
	margin-top: 1rem;
	margin-bottom: 1rem;
	justify-content: space-evenly;
	align-items: center;
}

.footer-link {
	color: #e0ae77;
	font-size: 0.9375rem;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

@media(hover: hover) {

	.footer-link:hover {
		color: #00ffff;
	}

}

.touch-link {
	color: #00ffff;
}

@media screen and (max-width: 700px) {

	footer {
		padding: 0.15rem;
		font-size: 0.75rem;
	}

	.footer-text {
		margin-left: 0.5rem;
		margin-right: 0.5rem;
	}

	.footer-nav {
		width: 10rem;
		margin-top: 0.5rem;
		margin-bottom: 0.5rem;
	}

	.footer-link {
		font-size: 0.75rem;
	}

}
